
ul li { padding: 5px 0px; }
img {max-width:100%;}

.flexwrap {
    display: flex;
    justify-content: center;
}

.contentwrap {
    margin: 10px;
    width: 70%;
    max-width: 700px;
}

.h1-text {
    color: darkslategrey;
    font-size: 3rem;
    margin: 20px;
    margin-left: 2px;
}

.h2-text {
    color: darkslategrey;
    font-size: 2rem;
    margin: 20px;
    margin-left: 2px;
}

.h3-text {
    color: darkslategrey;
    font-size: 1.5rem;
    margin: 20px;
    margin-left: 2px;
}

.h4-text {
    color: darkslategrey;
    font-size: 1rem;
    margin: 20px;
    margin-left: 2px;
}

.list-text {
    color: darkslategrey;
    font-size: 1rem;
    margin-left: 30px;
    margin-bottom: 0px;
}

.figcaption-text {
    color: darkslategrey;
    font-size: 1rem;
    margin-left: 2px;
    font-style: italic;
}

.log-text {
    font-size: 1.3rem;
}

.blog-text {
    font-size: 1.3rem;
}

.inline-img {
    margin-top:20px;
    margin-bottom:20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.custom-nav {
    display: flex;
    justify-content: space-between;
}

.nav-buttons {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 20px;
    display: flex;
    flex-direction: column;
}

.nav-buttons a {
    color: slategray;
}